<template>
  <div class="songlist-detail-wrapper" ref="wrapper">
    <music-list
      :name="name"
      :icon="imgSrc"
      :songLists="songList"
      @pullingUp="pullingUpHandle"
      ref="musicList"
    />
  </div>
</template>

<script>
import setBottom from 'mixin/scaleDomHeight/';
import { getRecommendSongList, getSongDetail } from 'api/song/song';
import MusicList from 'components/content/MusicList.vue';

export default {
  mixins: [setBottom],
  components: {
    MusicList,
  },
  props: {},
  data() {
    return {
      songList: [],
      songIdList: [],
      name: '',
      imgSrc: '',
      id: '',
      offset: 20,
    };
  },
  methods: {
    handlePlayList() {
      if (this.playList.length === 0) {
        this.$refs.wrapper.style.bottom = '0px';
      } else {
        this.$refs.wrapper.style.bottom = `${this.miniPlayerHeight}px`;
      }
    },
    pullingUpHandle() {
      console.log('1');
      const idArr = this.songIdList.splice(0, 20);
      if (idArr.length === 0) {
        return;
      }
      getSongDetail(idArr.join(','))
        .then((res) => {
          this.songList = [...this.songList, ...res];
          this.$nextTick(() => {
            this.$refs.musicList.finishPullUp();
          });
        })
        .catch((err) => {
          console.log('get song failed', err);
        });
    },
  },
  created() {
    this.name = this.$route.query.name;
    this.imgSrc = this.$route.query.imgSrc;
    this.id = this.$route.params.id;
    getRecommendSongList(this.id)
      .then((res) => {
        this.songList = res.songList;
        this.songIdList = res.songIdList.splice(0, 20);
      })
      .catch((err) => {
        console.log('song list get failed', err);
      });
  },
};
</script>
<style lang="scss" scoped>
  @import '~assets/style/variable.scss';
  .songlist-detail-wrapper {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background: $color-background;
  }
</style>
